'use client';
import { useState, useEffect } from 'react';
import { db } from '@/firebase/config';
import { collection, query, where, getDocs } from 'firebase/firestore';
import Image from "next/image";
import MenuBtn from "../menuBtn"

export default function PortfolioUpload() {
  const [targetSchools, setTargetSchools] = useState([]);

  useEffect(() => {
    const fetchTargetSchools = async () => {
      try {
        const schoolsRef = collection(db, 'targetSchools');
        const q = query(schoolsRef, where('userId', '==', 'currentUserId')); // Replace with actual user ID
        const querySnapshot = await getDocs(q);
        
        const schools = [];
        querySnapshot.forEach((doc) => {
          schools.push({ id: doc.id, ...doc.data() });
        });
        
        setTargetSchools(schools);
      } catch (error) {
        console.error('Error:', error);
      }
    };

    fetchTargetSchools();
  }, []);

  return (
    <div className="w-full h-full flex justify-center items-center bg-gray-300">
      
      {/* in public/html folder */}
      <iframe src="html/portfolioUpload.html" className="w-full h-full" />

      <div className="portfolio-sidebar">
        <h3 className="category-title">志願學校系所</h3>
        <ul className="category-list">
          <li className="category-item active" data-category="all">
            <i className="fas fa-list"></i> 所有文件
          </li>
          {targetSchools.map((school) => (
            <li key={school.id} className="category-item" data-category={school.code}>
              <i className="fas fa-university"></i> {school.name}
            </li>
          ))}
        </ul>
      </div>

    </div>
  );
}
